@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    @font-face {
        font-family: 'Poppins-Black';
        src: local('Poppins-Black'), url(../fonts/Poppins/Poppins-Black.ttf) format('ttf');
    }
    @font-face {
        font-family: 'Poppins-BoldItalic';
        src: local('Poppins-BoldItalic'), url(../fonts/Poppins/Poppins-BoldItalic.ttf) format('ttf');
    }
}

.abc{
    color: red;
}

.min-width-10px{
    min-width: 10px;
}

.max-width-400px{
    max-width: 400px;
}

.position-right-5vw{
    right: 5vw;
}

.max-width-243{
    max-width: 243px;
}

@screen md {
    .max-width-674px{
        max-width: 674px;
    }

    .global-container{
        padding-left: 7.083vw;
        padding-right: 7.083vw;
    }

    .billboard-container{
        padding-left: 7.29vw !important;
        padding-right: 3.8vw !important;
    }

    .max-width-1640px{
        max-width: 1640px;
    }
}



